import React, { useState } from "react"
function Card(props){
  const images = require.context('./images', true);
  const img = images(props.imgSrc).default
  return(
    <div className="card" onClick={props.HandleClick} data-attr={props.name}>
      <img src={img} alt="Ezz" data-attr={props.name}/>
    </div>
  )

}

export default Card