import './App.css';
import Card from "./Card"
import {dataArray} from "./data"
import React from "react"
import Heading from "./Heading"
function App() {
  const [data, setData] = React.useState(dataArray)
  const [clickedElements, setClicked] = React.useState([])
  const [currScore, setCurrScore] = React.useState(0)
  const [bestScore, setBestScore] = React.useState(0)

  function shuffleArray(array) {
    for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
    }
    return array
  } 

  function HandleClick(event){
    let array = [...data]
    array = shuffleArray(array)
    setData(array)
    let attr = event.target.getAttribute("data-attr")
    if(clickedElements.includes(attr)){
      setClicked([])
      let currentscore = currScore
      if(bestScore < currentscore)
      setBestScore(currentscore)
      setCurrScore(0)
    }
    else{
      clickedElements.push(attr)
      setCurrScore(currScore + 1)
    }
  }

  let result = data.map(item => {
    return <Card key={item.name} HandleClick={HandleClick} name={item.name} imgSrc={item.imgSrc}/>
  })
  return (
    <div className="App">
      <Heading currScore={currScore} bestScore={bestScore}/>
      {result}
    </div>
  )
}

export default App;
